import React from 'react'
import CarouselCom from '../../materialUI/Carousel'
import './Carousel.css'

import Fade from 'react-reveal/Fade';

const Our_Events = () => {
  return (
    <div className="carousel">
       <Fade bottom>
          <h1 className="eventstitle" > OUR EVENTS</h1>
        </Fade>
      <CarouselCom />
    </div>
  )
}

export default Our_Events