import React, {useState} from 'react';
import './Navbar.css'
import {motion} from 'framer-motion'
import Black_logo from '../../Images/black_logo.png'
export default function Main_navbar() {

  return (
    <div className='main_navbar'>
            <div className='logo_nav'>
              <img src={Black_logo} className="black_logo" alt="" srcset="" />
            </div>
           
                 <div className='nav-links'>
                    <motion.li whileHover={{y:-11, scale:1.1}}  transition={{duration:0.2}} className='nav-items'>HOME</motion.li>
                    <motion.li whileHover={{y:-11, scale:1.1}}  transition={{duration:0.2}} className='nav-items'>EVENTS</motion.li>
                     <motion.li whileHover={{y:-11, scale:1.1}}  transition={{duration:0.2}} className='nav-items'>SPONSORS</motion.li>
                    <motion.li whileHover={{y:-11, scale:1.1}}  transition={{duration:0.2}} className='nav-items'>TEAM</motion.li>
                    <motion.li whileHover={{y:-11, scale:1.1}}  transition={{duration:0.2}} className='nav-items'>CONTACT</motion.li>
                </div>
               
          
    </div>
  )
}
